/**
 * App Configuration. 3rd party URLs, db configs etc. here. 
 * Follow default naming of environments and add in local for local development. 
 * added APP_ENV to allow for unique environments and our properties for the app.
 */
const env = process.env.APP_ENV || 'local';

// default app version 
const appVersion = '1.0.0';

const local = {
    appVersion: appVersion,
    env: 'local'
};

const development = {
    appVersion: appVersion,
    env: 'development'
};

const test = {
    appVersion: appVersion,
    env: 'test'
};

const production = {
    appVersion: appVersion,
    env: 'production'
};

const config = {
    local, development, test, production
};

module.exports = config[env];
