import { AgGridColumn, AgGridReact } from 'ag-grid-react';
import './contractorGrid.css';

import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-balham.css';

const rowData = require('../data/contractorData.json').rowData;

function ContractorGrid() {
    return (
        <div className='ag-theme-balham' style={{ height: 900 }}>
            <AgGridReact defaultColDef={{
                sortable: true,
                resizable: true,
                filter: true
            }}
                groupHeaderHeight={25}
                headerHeight={25}
                headerClass={'cell-header'}
                rowHeight={35}
                rowData={rowData}

            >
                <AgGridColumn headerName='Contractor Contact' >
                    <AgGridColumn headerName='DBA' field='dba' width={250} headerClass={'cell-header'} cellClass={'cell-address'}></AgGridColumn>
                    <AgGridColumn headerName='Contact' field='contractor.name' width={200} headerClass={'cell-header'} cellClass={'cell-address'}></AgGridColumn>
                    <AgGridColumn headerName='Address' field='contractor.address' width={150} headerClass={'cell-header'} cellClass={'cell-address'}></AgGridColumn>
                    <AgGridColumn headerName='City' field='contractor.city' width={120} headerClass={'cell-header'} cellClass={'cell-address'}></AgGridColumn>
                    <AgGridColumn headerName='State' field='contractor.state' width={80} headerClass={'cell-header'} cellClass={'cell-address'} ></AgGridColumn>
                    <AgGridColumn headerName='Zip' field='contractor.zip' width={80} headerClass={'cell-header'} cellClass={'cell-address'}></AgGridColumn>
                    <AgGridColumn headerName='Phone' field='contractor.phone' width={150} headerClass={'cell-header'} cellClass={'cell-address'}></AgGridColumn>
                </AgGridColumn>
                <AgGridColumn headerName='Jurisdiction' headerClass={'cell-header'} >
                    <AgGridColumn headerName='W' field='jurisdiction.0' width={60} cellClass={'cell-license'} ></AgGridColumn>
                    <AgGridColumn headerName='LV' field='jurisdiction.1' width={60} cellClass={'cell-license'} ></AgGridColumn>
                    <AgGridColumn headerName='Co' field='jurisdiction.2' width={60} cellClass={'cell-license'} ></AgGridColumn>
                </AgGridColumn>
                <AgGridColumn headerName='License'>
                    <AgGridColumn headerName='Type' field='licenseType' width={80} cellClass={'cell-address'} headerTooltip='A - Unlimited B - Limited C - Homebuilder D - Specialty Trade'></AgGridColumn>
                    <AgGridColumn headerName='Type "D" Trade' field='typeDTrade' width={200} cellClass={'cell-address'}></AgGridColumn>
                    <AgGridColumn headerName='Expires' field='expires' cellClass={'cell-address'}></AgGridColumn>
                </AgGridColumn>
            </AgGridReact>
        </div>
    );
}

export default ContractorGrid;